.supporter-grid {
  display: flex;                       /* establish flex container */
  flex-wrap: wrap;                     /* enable flex items to wrap */
  justify-content: center; /* align horizontal */
  align-items: center;

  .supporter-cell {
    flex: 0 0 32%;                       /* don't grow, don't shrink, width */
    @media screen and (max-width: $supporter-break) {
      flex: 0 0 45%;
    }
    @media screen and (max-width: $supporter-break-single) {
      flex: 0 0 80%;
    }
    margin-top: 1em;
    margin-bottom: 1em;
    img {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }

    .supporter-img {
      width: revert;
      max-width: $supporter-img-max-width;
      max-height: 100px;
    }
  }
}
