// Header text feature
.call-out {
    @extend %padding-medium;
    display: inline-block;
    width: 100%;
    height: 100%;
    font-size: 1.2em;
    text-align: center;
    color: $header-desc-text-color;
    background: $header-desc-background-color no-repeat;
    background-size: $feature-image-size;

    /* --- Responsive --- */
    // @media screen and (max-width: 850px) {
    //     background-size: 100% auto;
    // }

    //For white images
    p,
    a,
    li,
    h1,
    h2,
    h3,
    h4,
    h5 {
        @extend .header-txt-shadow;
    }

    p:last-child {
        margin-bottom: 0;
    }
}

#main {
    background-position: 100% 0, 0 center, center top;
}

// Post listing
.posts {
    .post-teaser {
        @extend %padding-regular;
        width: 100%;
        margin-bottom: 0;
        max-width: 80em;
        margin-right: auto;
        margin-left: auto;
        display: block;
        header {
            margin-top: 10px;
            text-align: center;
        }
        background-size: $feature-image-size;
        border-bottom: 1px solid $border-color;
        p a {
            @extend .body-link;
        }
        img {
            width: 100%;
            padding: 0;
            vertical-align: middle;
        }

        .post-img {
            // vertical-align: middle;
            border-radius: 10px;
            overflow: hidden;
            object-fit: none;
            object-position: center;

            @media (min-width: $break) {
                height: 250px;
            } //Smaller screen
            @media screen and (max-width: $break) {
                height: 150px;
            }
        }

        .post-img img {
            //max-width: 100%;

            -moz-transition-timing-function: ease-out;
            -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;

            -moz-transition: all 2s;
            -webkit-transition: all 2s;
            transition: all 2s;
            transform: translateY(-30%);

            &:hover {
                transform: translateY(-30%) scale(1.1);
            }
        }
    }
    .excerpt {
        margin-top: 1em;
    }
}

// Post listing
.home-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: normal;
    margin: 1em;
    .homeposts {
        flex: 0 0 60%;
        margin-right: 1em;
        @media screen and (max-width: $break) {
            flex: 0 0 90%;
        }

        .announcement {
            padding: 1em 1.5em;
            background-color: #e9ecef;
            border-radius: .6em;         
            width: 100%;
            max-width: $text-width-max;
            margin-left: auto;
            margin-right: auto;
            display: block;
            h1 {
                margin: 0.2em 0;
            }
            h3 {
                margin: 0.5em 0;
            }
        }
        .homepost-teaser {
            border-bottom: 1px solid $border-color;
            width: 100%;
            max-width: $text-width-max;
            margin-left: auto;
            margin-right: auto;
            display: block;
            background-size: $feature-image-size;
            header {
                text-align: center;
            }
            p a {
                @extend .body-link;
            }
            img {
                width: 100%;
                padding: 0;
                vertical-align: middle;
            }
        }
        .homepost-excerpt {
            margin-top: 1em;
        }
    }
    .twitter {
        flex: 0 0 35%;
        @media screen and (max-width: $break) {
            flex: 0 0 90%;
        }
    }
}

// Pagination
.pagination {
    padding: $padding-small $padding-large 0 $padding-large;
    text-align: center;
    @media (max-width: 600px) {
        padding: $padding-small;
    }

    .button {
        margin: 0 1.5em;
        i {
            vertical-align: middle;
        }
    }

}
