.project-link {
    display: inline-block;

    .project-title {
        text-align: center;
        vertical-align: middle;
        color: black;
        font-size: 1.2em;
        font-weight: bold;
    }

    .project-img {
        display: block;
        img {
            width:100%;
            &:hover {
                filter: brightness(70%);
            }
        }
    }
}

.project-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

}
.project-cell {
  flex: 0 0 45%;
  margin-bottom: 10px;
  margin-left: 5px;
  margin-right: 5px;
}

.project-tag-list {
    font-size: 0.8em;
    opacity: 0.7;
    text-align: center;
}
